import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';


import { AppComponent } from './app.component';
import {HttpClientModule} from '@angular/common/http';
import { CarsComponent } from './components/cars/cars.component';
import { CarService } from './services/car.service';
import { CarHeaderComponent } from './components/car-header/car-header.component';
import {RouterModule, Routes} from '@angular/router';
import { ShowCarsComponent } from './components/cars/show-cars/show-cars.component';
import { AddCarsComponent } from './components/cars/add-cars/add-cars.component';
import { DeleteCarsComponent } from './components/cars/delete-cars/delete-cars.component';
import { UpdateCarsComponent } from './components/cars/update-cars/update-cars.component';
import {FormsModule} from '@angular/forms';


const myRoute: Routes = [
  {path: 'show', component: ShowCarsComponent},
  {path: 'add', component: AddCarsComponent},
  {path: 'delete', component: DeleteCarsComponent},
  {path: 'update', component: UpdateCarsComponent}
];


@NgModule({
  declarations: [
    AppComponent,
    CarsComponent,
    CarHeaderComponent,
    ShowCarsComponent,
    AddCarsComponent,
    DeleteCarsComponent,
    UpdateCarsComponent
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    RouterModule.forRoot(myRoute),
    FormsModule
  ],
  bootstrap: [AppComponent],
  providers: [CarService]
})
export class AppModule { }



