import { Component, OnInit } from '@angular/core';
import {CarService} from '../../../services/car.service';
import {CarModel} from '../../../model/CarModel';
import {ActivatedRoute, Router} from '@angular/router';
import {Observable} from 'rxjs/Observable';

@Component({
  selector: 'app-show-cars',
  templateUrl: './show-cars.component.html',
  styleUrls: ['./show-cars.component.css']
})
export class ShowCarsComponent implements OnInit {
  carObj: CarModel = {
    id: null, carName: '', carModel: '', carPrice: null
  };
  carArray: CarModel[];
  updateCar: boolean;
  constructor(private ser: CarService) { }

  ngOnInit() {
    this.ser.getCars().subscribe(
      car => {
        this.carArray = car;
      }
    );
  }

  updateCars(car: CarModel) {
    this.carObj = car;
  }
  updateCarsMethod() {
    this.ser.updateCar(this.carObj).subscribe(
      carr => {
        console.log(carr);
      }
    );
  }

}



