import { Component, OnInit } from '@angular/core';
import {CarService} from '../../../services/car.service';
import {ActivatedRoute, Router} from '@angular/router';
import {CarModel} from '../../../model/CarModel';

@Component({
  selector: 'app-delete-cars',
  templateUrl: './delete-cars.component.html',
  styleUrls: ['./delete-cars.component.css']
})
export class DeleteCarsComponent implements OnInit {
  carArray: CarModel[];
  carDelete: CarModel = {
    id: null, carName: '', carModel: '', carPrice: null
  };
  updateCar: boolean;
  constructor(private ser: CarService, private rou: ActivatedRoute, private rout: Router) { }

  ngOnInit() {
    this.ser.getCars().subscribe(
      car => {
        this.carArray = car;
      }
    );
  }
  removeCar(car: CarModel) {
    this.carDelete = car;
    console.log(car);
  }
  removeCarMethod() {
    this.ser.deleteCar(this.carDelete.id).subscribe();
    this.carArray = this.carArray.filter(item => item !== this.carDelete);
  }
}



