import { Injectable } from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable} from 'rxjs/Observable';
import {CarModel} from '../model/CarModel';
const httpOption = {
  headers: new HttpHeaders({'Content-Type': 'application/json'})
};
@Injectable()
export class CarService {
  carsUrl = 'http://localhost:1111/cars';
  constructor(private http: HttpClient) { }

  getCars(): Observable<CarModel[]> {
    return this.http.get<CarModel[]>(this.carsUrl);
  }
  updateCar(car: CarModel): Observable<CarModel> {
    const url = `${this.carsUrl}/${car.id}`;
    return this.http.put<CarModel>(url, car, httpOption);
  }
  deleteCar(car: CarModel | number): Observable<CarModel> {
    const id = typeof car === 'number' ? car : car.id;
    const url = `${this.carsUrl}/${id}`;
    return this.http.delete<CarModel>(url, httpOption);
  }
}



